<script>
	export default {
	  name:'detail',
	  props:['data','collectionName'],
	  data(){
		  return{
			  detail:[]
		  }
	  },
	  mounted(){
	  let collectionName=this.$route.query.collectionName;
	  let _id=this.$route.params._id;
	  this.$axios({
	    url:`/api/goods/${collectionName}/${_id}`,
	  }).then(
	    res=>this.detail=res.data.data
	  )
	},
	  }
	  
	
</script>

